.widgetBar {
	position: fixed;
	top: 40%;
	left: 30%;
	width: 40vw; /* Ширина виджета */
	height: 300px;
	background-color: #f0f0f0; /* Цвет фона */
	border-radius: 40px;
	background: url(https://grainy-gradients.vercel.app/noise.svg),
		linear-gradient(90deg, rgba(56, 255, 16, 1) 0%, rgba(255, 113, 179, 1) 84%);
	justify-content: center;
	align-items: center;
	display: flex;
	z-index: 12;
}

.widgetContent {
	overflow: hidden;
	display: grid;
	position: relative;
	background-color: black; /* Цвет фона */
	border-radius: 40px;
	width: 39.8vw; /* Ширина виджета */
	height: 297px;
	justify-content: center;
	align-items: center;
}

.fontWidget {
	color: rgb(255, 255, 255);
}

.titlesBlock {
	justify-items: center;
	align-items: center;
	display: grid;
	width: 100%;
}
.svg {
	height: 30px;
	width: 30px;
	stroke: aqua;
	fill: url(https://grainy-gradients.vercel.app/noise.svg),
		linear-gradient(90deg, rgba(56, 255, 16, 1) 0%, rgba(255, 113, 179, 1) 84%);
}

.oneTitleBlock {
	height: 600px;
	height: 20px;
}

.infoBlock {
	display: grid;
	gap: 0px;
	width: 98%;
	height: 50%;
}
